import { Component, OnInit, Input } from '@angular/core';

import {UserService, ProductService, BasketService } from '../../../shared/index';

import * as jQuery from 'jquery'; 

@Component({
    selector: 'product-details-test',
    templateUrl: './product-details.component.html',
})
export class ProductDetailsTestComponent implements OnInit {

    public product: any;
    public userBasket: any;

    constructor(private userSvc: UserService,
        private productSvc: ProductService,
        private basketSvc: BasketService) {
    }

    ngOnChanges() {
        //this.product = this.productSvc.product;
    }

    ngOnInit() {       
    }

    ngDoCheck() {
        this.product = this.productSvc.product;
        this.userBasket = this.basketSvc.userBasket;
    }

    ngAfterContentInit() {
       
        jQuery('#userBasket').insertBefore('#main-container');
        jQuery('#productFamilyTabs').insertBefore('#main-container');
    }

    ngAfterViewChecked() {     
    }

    ngOnDestroy() {
        jQuery('#content > #userBasket').remove();
        jQuery('#content > #productFamilyTabs').remove();
    }
};


